// PlayerSprite 컴포넌트 수정
const PlayerSprite = ({ direction = 'down' }) => {
  const getPlayerImage = () => {
    switch (direction) {
      case 'left':
        return santaLeft;
      case 'right':
        return santaRight;
      default: // down, up 모두 정면 이미지 사용
        return santaFront;
    }
  };

  return (
    <img 
      src={getPlayerImage()} 
      alt="산타"
      className="w-16 h-16"
      style={{ 
        imageRendering: 'pixelated',
        transform: 'translateY(-8px)' // 캐릭터를 약간 위로 올려서 타일과 정렬
      }}
    />
  );
};

